/** @jsx React.DOM */

var React = require("react/addons");
var ModalActions = require("editor/actions/ModalActions");

var Modal = React.createClass({displayName: 'Modal',

  handleCloseModal: function() {
    ModalActions.close();
  },

  handleApply: function() {
    this.props.onApply();
    ModalActions.close();
  },

  render: function() {
    var modalStyle = {
      position: "absolute",
      "zIndex": "100"
      // top: this.props.y,
      // left: this.props.x
    };

    return (
      React.DOM.div({className: "modal-dialog", style: modalStyle}, 
        React.DOM.div({className: "modal-content"}, 
          React.DOM.div({className: "modal-header"}, 
            React.DOM.button({type: "button", className: "close", 'data-dismiss': "modal", onClick: this.handleCloseModal}, 
              React.DOM.span({'aria-hidden': "true"}, "×"), React.DOM.span({className: "sr-only"}, "Cancel")
            ), 
            React.DOM.h4({className: "modal-title"}, this.props.title)
          ), 
          React.DOM.div({className: "modal-body"}, 
            this.props.children
          ), 
          React.DOM.div({className: "modal-footer"}, 
            React.DOM.form({action: ""}, 
              React.DOM.button({type: "button", className: "btn btn-default", 'data-dismiss': "modal", onClick: this.handleCloseModal}, "Cancel"), 
              React.DOM.button({type: "button", onClick: this.handleApply, className: "btn btn-primary"}, "Apply")
            )
          )
        )
      )
    );
  }
});

module.exports = Modal;





