/** @jsx React.DOM */

var React = require("react/addons");

var ContextMenu = React.createClass({displayName: 'ContextMenu',
  render: function() {
    var menuStyle = {
      position: "absolute",
      top: this.props.y,
      left: this.props.x
    };

    return (
      React.DOM.div({style: menuStyle, className: "open"}, 
        React.DOM.ul({className: "dropdown-menu", role: "menu"}, 
          this.props.children.reduce(function(acc, group, index, array) {
            var boundedGroup = group.map(function(item) {
              return React.DOM.li(null, React.DOM.a({href: "#", onClick: item.onClick}, item.title));
            })

            if (array.length !== index + 1) {
              boundedGroup.push(React.DOM.li({className: "divider"}));
            }

            return acc.concat(boundedGroup);
          }, [])
        )
      )
    );
  }
});

module.exports = ContextMenu;




