/** @jsx React.DOM */

var _ = require("lodash");
var Immutable = require("immutable");
var React = require("react/addons");

var TreeActions = require("editor/actions/TreeActions");

var Leaf = React.createClass({displayName: 'Leaf',
    handleOpenFile: function(tree, e) {
        TreeActions.openFile(tree);
    },

    handleContextMenu: function(id, type, e) {
        e.preventDefault();
        TreeActions.openContextMenu(id, type, e.clientX, e.clientY);
    },

    render: function() {
        var leaf = this.props.leaf;

        if (undefined == leaf) {
            return null;
        }

        return (
            React.DOM.li({className: "tree-item", 'data-template': "treeitem", role: "treeitem"}, 
                React.DOM.button({className: "tree-item-name", 
                    onContextMenu: this.handleContextMenu.bind(this, leaf.id, "file"), 
                    onDoubleClick: this.handleOpenFile.bind(this, leaf)}, 
                    React.DOM.span({className: "glyphicon icon-item fueluxicon-bullet"}), 
                    React.DOM.span({className: "tree-label"}, leaf.name)
                )
            )
        );
    }
});

module.exports = Leaf;



